class Slugifier {
  public slugify(text: string): string {
    text = text.toString().toLowerCase().trim()

    const sets = [
      { to: 'a', from: '[]' },
      { to: 'c', from: '[]' },
      { to: 'd', from: '[]' },
      { to: 'e', from: '[]' },
      { to: 'g', from: '[]' },
      { to: 'h', from: '[]' },
      { to: 'i', from: '[]' },
      { to: 'j', from: '[]' },
      { to: 'ij', from: '[]' },
      { to: 'k', from: '[]' },
      { to: 'l', from: '[]' },
      { to: 'm', from: '[]' },
      { to: 'n', from: '[]' },
      { to: 'o', from: '[]' },
      { to: 'oe', from: '[]' },
      { to: 'p', from: '[]' },
      { to: 'r', from: '[]' },
      { to: 's', from: '[]' },
      { to: 't', from: '[]' },
      { to: 'u', from: '[]' },
      { to: 'w', from: '[]' },
      { to: 'x', from: '[]' },
      { to: 'y', from: '[]' },
      { to: 'z', from: '[]' },
      { to: '-', from: '[/_,:;\']' },
    ]

    sets.forEach((set) => {
      text = text.replace(new RegExp(set.from, 'gi'), set.to)
    })

    text = text
      .toString()
      .toLowerCase()
      .replace(/\s+/g, '-') // Replace spaces with -
      .replace(/&/g, '-and-') // Replace & with 'and'
      .replace(/[^\w\-]+/g, '') // Remove all non-word chars
      .replace(/\--+/g, '-') // Replace multiple - with single -

    return text
  }
}

export default new Slugifier()
